import React from 'react';
import { createRoot } from 'react-dom/client';
import './index.css';
import './styles.scss';
import reportWebVitals from './reportWebVitals';
import UserBlock from './UserBlock';
import MyBids from './MyBids';
import { ChakraProvider } from '@chakra-ui/react';

const userBlockContainer = document.getElementById('user-block');
const myBidsContainer = document.getElementById('my-bids');

if (userBlockContainer) {
  const userBlockRoot = createRoot(userBlockContainer);
  userBlockRoot.render(
    <ChakraProvider>
      <UserBlock />
    </ChakraProvider>
  );
}

if (myBidsContainer) {
  const myBidsRoot = createRoot(myBidsContainer);
  myBidsRoot.render(
    <ChakraProvider>
      <MyBids />
    </ChakraProvider>
  );
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

